import React from 'react';
import PropTypes from 'prop-types';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core';
import { CssBaseline } from '@material-ui/core';
import { Toolbar, Drawer, Content, Settings } from './components';
import { getModeState } from './persistence/state';
import { withStyles, classNames } from './utils/styles';
import { getMainColourForMode } from './utils/resource-loader';

class App extends React.Component {
  static propTypes = {
    classes: PropTypes.object.isRequired,
    store$: PropTypes.object.isRequired
  };
  constructor(props) {
    super(props);
    this.state = {};
    this.subscription = undefined;
  }
  componentDidMount() {
    this.subscription = this.props.store$.subscribe(state => {
      this.setState({...state, ready: true});
    });

  }
  componentWillUnmount() {
    if (!!this.subscription) {
      this.subscription.unsubscribe();
      this.subscription = undefined;
    }
  }

  render() {
    if (!this.state.ready) {
      return <div></div>;
    }
    const { classes } = this.props;
    const { navigation = {}, unlockables = {}, mode = {} } = getModeState(this.state);

    const theme = {
      palette: {
        type: mode.theme || 'light',
        primary: {
          main: getMainColourForMode(mode) || '#34495e'
        },
        secondary: {
          main: '#e74c3c'
        },
        contrastThreshold: 3,
        tonalOffset: 0.1
      }
    };

    return (
      <MuiThemeProvider theme={createMuiTheme(theme)}>
        <CssBaseline />
        <Toolbar className={classes.fixed} mode={mode} navigation={navigation} />
        <div className={classNames(classes.hbox, classes.noOverflow)}>
          <Drawer unlockables={unlockables} mode={mode} />
          <Content navigation={navigation} mode={mode} />
        </div>
        <Settings {...mode} />
      </MuiThemeProvider>
    );
  }
}

export default withStyles()(App);
