export const HOME_TEXT = (
  <React.Fragment>
    <p>You are Aeneas. You wake up in the middle of the night because of a dreadful clamour. In a haste, you only manage to grab your journal. As you leave the room, in an attempt to stop you from helping the Trojans, Juno wipes your memory. You need to piece together what has happened to you so far and see where Fate takes you next. You need to act, speak and think like Aeneas.</p>
    <p>From this point onwards, you, a great hero, will be faced with many tasks. You do not need to carry out every task. But you must complete <strong>at least one</strong> activity from each section in order to gain a prompt. These prompts are needed to summon Hermes (Mercury) who will deliver your next mission. Oh, and it would be useful to know that sometimes the gods may set you compulsory tasks – if you don’t do these, you can’t move on.</p>
    <p>On your way, you should aim to collect as many Nectar Points as possible. You don’t know when you’ll need to use them to please the gods! The gods live off nectar, so I’m sure that they will come in handy.</p>
    <p>Don’t forget to keep a record of things in your journal!</p>
    <p>Now hurry on your way hero!</p>
  </React.Fragment>
);

