import React from 'react';
import Button from '@material-ui/core/Button';

import { withStyles } from '../../utils/styles';
import { Typography } from '@material-ui/core';

import { unlockShop } from '../../utils/state-api';

const styles = theme => ({
  button: {
    margin: theme.spacing(1)
  }
})
class Shop extends React.Component {
  unlockShop = (unlock) => {
    if (!!unlock) {
      unlockShop();
      alert('You have unlocked the shop!');
    } else {
      alert('No problem!');
    }
  }
  render() {
    const { classes, description, options } = this.props;
    return (
    <div className={classes.vbox}>
      <Typography>{description}</Typography>
      {options.map(({description, unlock}) => <Button key={description} variant='contained' color='primary' className={classes.button} onClick={() => this.unlockShop(unlock)}>{description}</Button>)}
    </div>
    );
  }
}

export default withStyles(styles)(Shop);