import { applySubstate, changeMode, toggleTheme as toggleTheme1, setPassword as setPassword1 } from '../persistence/state';
import { IDS } from '../views';
const { HOME_ID, SHOP_ID, PASSWORD_ID, EXERCISE_ID } = IDS;

export const unlockShop = () => {
  applySubstate('unlockables', {shopUnlocked: true});
}

const navigate = nextView => {
  applySubstate('navigation', {currentView: nextView})
}

export const navigateExercise = title => navigate({...EXERCISE_ID, title});
export const navigateHome = () => navigate(HOME_ID);
export const navigateShop = () => navigate(SHOP_ID);
export const navigatePassword = () => navigate(PASSWORD_ID);

export const setMode = ({selectedLevel, selectedText}) => {
  changeMode({selectedLevel, selectedText, showPrompt: false});
  navigate(HOME_ID);
}

export const showChangeModePrompt = open => {
  changeMode({showPrompt: !!open});
}

export const toggleTheme = () => toggleTheme1(); // TODO
export const setPassword = password => setPassword1(password); // TODO
export function createStateApi() {
  return {
    navigateHome,
    navigateShop,
    navigatePassword,
    navigateExercise,
    setPassword,
    setMode,
    showChangeModePrompt,
    toggleTheme,
    unlockShop
  };
}